/*---
title: Alignment
section: Utilities
---*/

:root {
  --tenth: .1;
  --eighth: .125;
  --sixth: .1666;
  --fifth: .2;
  --quarter: .25;
  --third: .3;
  --half: .5;
  --double: 2;
  --triple: 3;
}


/*------------------------------------*\
  #GENERAL-ALIGNMENT
\*------------------------------------*/

.u-start { @apply --start; }
.u-center { @apply --center; }
.u-end { @apply --end; }
.u-text-center { @apply --text-center; }
.u-text-middle { @apply --text-middle; }
.u-top { align-items: flex-start; }
.u-middle { align-items: center; }
.u-bottom { align-items: flex-end; }
.u-around { justify-content: space-around; }
.u-between { justify-content: space-between; }
.u-center-middle { @apply --center-middle; }


/*------------------------------------*\
  #LEADER-MARGIN
\*------------------------------------*/

.u-leader--tenth {
  margin-top: calc(var(--global-line-height) * .1) !important;
}

.u-leader--eighth {
  margin-top: calc(var(--global-line-height) * var(--eighth)) !important;
}

.u-leader--sixth {
  margin-top: calc(var(--global-line-height) * .1666) !important;
}

.u-leader--fifth {
  margin-top: calc(var(--global-line-height) * .2) !important;
}

.u-leader--quarter {
  margin-top: calc(var(--global-line-height) * .25) !important;
}

.u-leader--third {
  margin-top: calc(var(--global-line-height) * .3) !important;
}

.u-leader--half {
  margin-top: calc(var(--global-line-height) * .5) !important;
}

.u-leader {
  margin-top: var(--global-line-height) !important;
}

.u-leader--double {
  margin-top: calc(var(--global-line-height) * 2) !important;
}

.u-leader--triple {
  margin-top: calc(var(--global-line-height) * 3) !important;
}


/*------------------------------------*\
  #TRAILER-MARGIN
\*------------------------------------*/

.u-trailer--tenth {
  margin-bottom: calc(var(--global-line-height) * .1) !important;
}

.u-trailer--eighth {
  margin-bottom: calc(var(--global-line-height) * var(--eighth)) !important;
}

.u-trailer--sixth {
  margin-bottom: calc(var(--global-line-height) * .1666) !important;
}

.u-trailer--fifth {
  margin-bottom: calc(var(--global-line-height) * .2) !important;
}

.u-trailer--quarter {
  margin-bottom: calc(var(--global-line-height) * .25) !important;
}

.u-trailer--third {
  margin-bottom: calc(var(--global-line-height) * .3) !important;
}

.u-trailer--half {
  margin-bottom: calc(var(--global-line-height) * .5) !important;
}

.u-trailer {
  margin-bottom: var(--global-line-height) !important;
}

.u-trailer--double {
  margin-bottom: calc(var(--global-line-height) * 2) !important;
}

.u-trailer--triple {
  margin-bottom: calc(var(--global-line-height) * 3) !important;
}


